<template>
    <div class="bodyList">
        <div class="artical" v-for="(item, index) in drop" :key="index">

            <div class="dotts"></div>

            <a class="date" :href="item.url">
                <span class="trig"></span>
                <span class="dates" v-text="item.date"></span>
            </a>

            <div class="art-container">
                <div class="line"></div>
                <div class="art-content">
                <p class="art-title text_initial">
                    <router-link :to="item.url">{{item.tips}}</router-link>
                </p>
                <div class="art-img">
                    <img :src="originImage + item.img" :alt="item.alt" />
                </div>
                <div class="art-detail text_initial">
                    <p>{{item.introduce}}</p>
                </div>
                </div>
            </div>

        </div>
    </div>
</template>
<script>
export default {
  name: "dropBYdrop",
  props: {
    "drop": [String, Object],
  },
  data() {
    return {
      originImage: "",
    };
  },
  mounted() {
    // console.log(this.drop);
    this.originImage = this.$store.state.origin + this.$store.state.image;

  },
};
</script>